import Vue from 'vue'
import VueRouter from 'vue-router'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: () => import('../views/login')
  },
  {
    path: '/home',
    name: 'home',
    component: () => import('../views/home'),
    meta: {
      title: '首页',
    },
    children: [
      {
        /**认证管理 */
        path: '/realNameAuthentication',
        name: 'realNameAuthentication',
        component: () => import('../views/home/authentication/'),
        meta: {
          title: '认证管理',
        },
        children: [
          {
            /**实名认证 */
            path: '/realNameAuthentication',
            name: 'realNameAuthentication',
            component: () => import('../views/home/authentication/realNameAuthentication/'),
            meta: {
              title: '实名认证',
            },
          },
          {
            /**企业认证 */
            path: '/enterpriseAuthentication',
            name: 'enterpriseAuthentication',
            component: () => import('../views/home/authentication/enterpriseAuthentication/'),
            meta: {
              title: '企业认证',
            },
          },
          {
            /**押金认证 */
            path: '/depositAuthentication',
            name: 'depositAuthentication',
            component: () => import('../views/home/authentication/depositAuthentication/'),
            meta: {
              title: '押金认证',
            },
          },
        ]
      },
      {
        /**世界聊天 */
        path: '/worldChat',
        name: 'worldChat',
        component: () => import('../views/home/worldChat/'),
        meta: {
          title: '世界聊天',
        },
      },
      {
        /**用户管理 */
        path: '/userManage',
        name: 'userManage',
        component: () => import('../views/home/userManage/'),
        meta: {
          title: '用户管理',
        },
      },
      {
        /**用户管理 */
        path: '/userManage',
        name: 'userManage',
        component: () => import('../views/home/userManage/'),
        meta: {
          title: '用户管理',
        },
      },
      {
        /**客服管理 */
        path: '/customerManage',
        name: 'customerManage',
        component: () => import('../views/home/customerManage/'),
        meta: {
          title: '客服管理',
        },
      },
      {
        /**聊天管理 */
        path: '/chatManage',
        name: 'chatManage',
        component: () => import('../views/home/chatManage/'),
        meta: {
          title: '聊天管理',
        },
        children: [
          {
            /**私聊管理 */
            path: '/privateManage',
            name: 'privateManage',
            component: () => import('../views/home/chatManage/privateManage'),
            meta: {
              title: '私聊管理',
            },
          },
          {
            /**群聊管理 */
            path: '/groupChatManage',
            name: 'groupChatManage',
            component: () => import('../views/home/chatManage/groupChatManage'),
            meta: {
              title: '群聊管理',
            },
          },
          {
            /**违规管理 */
            path: '/violationManage',
            name: 'violationManage',
            component: () => import('../views/home/chatManage/violationManage/'),
            meta: {
              title: '违规管理',
            },
          },
        ]
      },
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

